<template>
    <v-layout row>
        <v-text-field
                v-model="person.name"
                :rules="nameRules"
                label="">
        </v-text-field>
        <v-spacer></v-spacer>
        <v-text-field
                v-model="person.tel"
                :rules="telRules"
                label="">
        </v-text-field>
        <v-spacer></v-spacer>
        <v-text-field outline
                      v-model="person.comment"
                      :rules="commRules"
                      label="">
        </v-text-field>
        <v-spacer></v-spacer>
    </v-layout>
</template>

<script>
    import { mask } from 'vue-the-mask'
    export default {
        props: ["person"],
        directives: {
            mask,
        },
        name: "PersonInfo",
        data() {
            return {
                telRules: [
                    v => !!v || '  ',
                    v => (v && v.length === 19) || '   ',
                ],
                nameRules: [
                    v => !!v || '  ',
                    v => (v && v.length <= 500) || '   500 '
                ],
                commRules: [
                    v => v.length <= 500 || '   500 '
                ],
            }
        }
    }
</script>

