<template>
    <div>
        <v-text-field v-model="person.firstName"
                      :rules="nameRules"
                      label=""
                      required>
        </v-text-field>
        <v-text-field v-model="person.lastName"
                      :rules="nameRules"
                      label=""
                      required>
        </v-text-field>
        <v-text-field v-model="person.otchestvo"
                      :rules="nameRules"
                      label=""
                      required>
        </v-text-field>
        <v-text-field v-model="person.tel"
                      :rules="telRules"
                      :mask="telMask"
                      label=""
                      required>
        </v-text-field>
        <v-text-field v-model="person.email"
                      :rules="emailRules"
                      label="  "
                      required>
        </v-text-field>
    </div>
</template>

<script>
    export default {
        props: ["person"],

        name: "CommonForm",
        data() {
            return {
                telRules: [
                    v => !!v || '  ',
                    v => (v && v.length === 19) || '   ',
                ],
                nameRules: [
                    v => !!v || '  ',
                    v => (v && v.length <= 500) || '   500 '
                ],
                emailRules: [
                    v => !!v || 'E-mail ',
                    v => /.+@.+/.test(v) || '  E-mail',
                ],
            }
        },
        created() {
            this.telMask= '+375 (##) ###-##-##'
        }
    }
</script>

